.footer {
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 46px;
    position: relative;
    overflow: hidden;
    // margin-top: 32px;
    // padding: 0;


    @media screen and (min-width: 768px) {
        padding-bottom: 83px;

    }

    @media screen and (min-width: 1280px) {
        padding-bottom: 232px;
    }

}


.social {
    display: flex;
    justify-content: center;
    gap: 16px;
    margin: 0;
    margin-top: 16px;
    margin-bottom: 32px;

    @media screen and (min-width: 768px) {
        margin-bottom: 60px;
    }

    @media screen and (min-width: 1280px) {
        margin-bottom: 73px;
    }
}

.social__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;

    border-radius: 50%;
    fill: var(--primary-text-color);

    transition-property: fill, background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        fill: var(--white-color);
        background-color: var(--buttom-bcg-color);
    }
}

.contact-nav {
    display: flex;
    justify-content: space-between;
    @include FontWSL(400, 16px, 1.25);

    & a {
        color: var(--primary-text-color);
        transition-property: fill, color;
        transition-duration: 250ms;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

        &:hover,
        &:focus {
            color: var(--buttom-bcg-color);
            fill: var(--buttom-bcg-color);
        }
    }

    @media screen and (min-width: 768px) {
        padding-right: 130px;
    }

    @media screen and (min-width: 1280px) {
        padding-left: 107px;
        padding-right: 304px;
    }
}

.flour {
    height: 0;
}

.flour picture {
    position: absolute;
    top: 50px;
    right: 0px;

    z-index: -1;

    animation-name: spin;
    animation-duration: 5s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;


    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }

        50% {
            transform: rotate(8deg);
        }

        50% {
            transform: rotate(-10deg);
        }
    }

    @media screen and (min-width: 768px) {
        position: relative;
        display: block;
        top: -110px;
        right: -550px;
    }


    @media screen and (min-width: 1280px) {
        top: -140px;
        right: -1000px;
    }
}